

export type TDSMark = string | TDSMarkObj;

export interface TDSMarkObj {
  style?: object;
  label: string;
}

export class TDSMarks {
  [key: string]: TDSMark;
}

/**
 * Processed steps that would be passed to sub components.
 */
export interface TDSExtendedMark {
  value: number;
  offset: number;
  config: TDSMark;
}

/**
 * Marks that would be rendered.
 */
export interface TDSDisplayedMark extends TDSExtendedMark {
  active: boolean;
  label: string;
  style?: object;
}

/**
 * Steps that would be rendered.
 */
export interface TDSDisplayedStep extends TDSExtendedMark {
  active: boolean;
  style?: object;
}

export type TDSSliderShowTooltip = 'always' | 'never' | 'default';

export type TDSSliderValue = number[] | number;

export interface TDSSliderHandler {
  offset: number | null;
  value: number | null;
  active: boolean;
}
