

import { Direction } from '@angular/cdk/bidi';
import { BooleanInput, NumberInput } from '@angular/cdk/coercion';
import { ChangeDetectionStrategy, Component, Input, OnChanges, ViewEncapsulation } from '@angular/core';
import { InputBoolean, InputNumber } from 'tds-ui/shared/utility';



export interface TDSSliderTrackStyle {
  bottom?: string | null;
  height?: string | null;
  left?: string | null;
  right?: string | null;
  width?: string | null;
  visibility?: string;
}

@Component({
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  selector: 'tds-slider-track',
  exportAs: 'tdsSliderTrack',
  preserveWhitespaces: false,
  template: ` 
  <div class="tds-slider-track absolute  rounded-full transition-colors duration-300 bg-primary-1 dark:bg-d-primary-1"
   [ngStyle]="style"
   [ngClass]="{'h-2':!vertical, 'w-2':vertical}"
   ></div> 
  `
})
export class TDSSliderTrackComponent implements OnChanges {
  static ngAcceptInputType_offset: NumberInput;
  static ngAcceptInputType_length: NumberInput;
  static ngAcceptInputType_vertical: BooleanInput;
  static ngAcceptInputType_included: BooleanInput;
  static ngAcceptInputType_reverse: BooleanInput;

  @Input() @InputNumber() offset: number = 0;
  @Input() @InputBoolean() reverse: boolean = false;
  @Input() dir: Direction = 'ltr';
  @Input() @InputNumber() length: number = 0;
  @Input() @InputBoolean() vertical = false;
  @Input() @InputBoolean() included = false;

  style: TDSSliderTrackStyle = {};

  ngOnChanges(): void {
    const vertical = this.vertical;
    const reverse = this.reverse;
    const visibility = this.included ? 'visible' : 'hidden';
    const offset = this.offset;
    const length = this.length;

    const positonStyle: TDSSliderTrackStyle = vertical
      ? {
        [reverse ? 'top' : 'bottom']: `${offset}%`,
        [reverse ? 'bottom' : 'top']: 'auto',
        height: `${length}%`,
        visibility
      }
      : {
        ...this.getHorizontalStylePosition(),
        width: `${length}%`,
        visibility
      };

    this.style = positonStyle;
  }

  private getHorizontalStylePosition(): { left: string; right: string } {
    let left = this.reverse ? 'auto' : `${this.offset}%`;
    let right = this.reverse ? `${this.offset}%` : 'auto';
    if (this.dir === 'rtl') {
      const tmp = left;
      left = right;
      right = tmp;
    }
    return { left, right };
  }
}
